import React, { Component } from 'react';
import './App.css';
import { getPorts, requestPort } from './SerialPort';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      error: null,
      jobOrder: '',
      beacons: '',
    };
    this.port = null;

    this.handleConnect = this.handleConnect.bind(this);
    this.handleDisconnect = this.handleDisconnect.bind(this);
    this.getDevice = this.getDevice.bind(this);
  }

  componentDidMount() {
    getPorts().then(ports => {
      if (ports.length === 0) {
        this.setState({ports: 'No devices found.'});
      } else {
        console.log(ports);
        this.port = ports[0];
        this.connect();

        this.port.subscriber('beacons', (data) => {
          this.setState({ beacons: data });
        });

        this.port.subscriber('jobOrder', (data) => {
          this.setState({ jobOrder: data });
        });
      }
    });
  }


  connect() {
    console.log(`connecting to  ${this.port.device_.productName}...`);
    this.port.connect().then((error) => {
      console.log(`connected...`);
    }).catch(error => {
      this.setState({ error: `Connection error: ${error}` });
    });
  }

  getDevice() {
    console.log('getDevice');
    getPorts().then(ports => {
      if (ports.length === 0) {
        this.setState({ error: 'No devices found.' });
      } else {
        console.log(ports);
        this.port = ports[0];
        this.connect();
      }
    });    
  }

  handleConnect() {
    if (this.port) {
      this.port.disconnect();
    } else {
      requestPort().then(selectedPort => {
        console.dir(selectedPort);
        this.port = selectedPort;
        this.connect();
      }).catch(error => {
        this.setState({ error: `Connection error: ${error}` });
      });
    }
  }

  handleDisconnect() {
    this.port.disconnect();
  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>WebUSB Demo</h2>
        </div>
        <br/>
        { this.state.error ? <div>{this.state.error}</div> : null }
        <br/>
        <button onClick={this.getDevice}>get device</button>
        <button onClick={this.handleConnect}>connect device</button>
        <button onClick={this.handleDisconnect}>disconnect device</button>
        <br/>
        <h2>Beacons</h2>
        <code>{ this.state.beacons === '' ? null : JSON.stringify(this.state.beacons) }</code>
        <h2>Job Orders</h2>
        <code>{ this.state.jobOrder === '' ? null : JSON.stringify(this.state.jobOrder) }</code>
      </div>
    );
  }
}

export default App;



