import Emmiter from './Emmiter';

const RE_SCANNED_CRC = /^(.+)::([0-9A-F]{2})$/;
const RE_BEACON_NUMBER = /^[0-9A-F]{4}-[0-9A-F]{4}$/;
const BYTE_LENGTH = 64;

const serialPort = class SerialPort {
  constructor(device) {
    this.device_ = device;
    this.emmiter_ = new Emmiter();
    this.rawScan_ = '';
    this.json_ = {};
  }

  connect() {    
    let readLoop = () => {
      let parts = [];
      this.device_.transferIn(3, BYTE_LENGTH).then(result => { // todo: set endpoint and byte length from interface
        if (result.data.byteLength > 0) {
          let textDecoder = new TextDecoder();
          let decodeData = textDecoder.decode(result.data);

          this.rawScan_ += decodeData.trim();
        }

        /* check for end of scan by looking for checksum */
        parts = this.rawScan_.match(RE_SCANNED_CRC);
        if (parts !== null && parts[2]) {
          this.onReceived(parts);
        }
        readLoop();
      }).catch(err => console.log('Scan error: ', err));      
    };

    return this.device_.open()
      .then(() => {
        console.log('open: ', this.device_.configuration);
        if (this.device_.configuration === null) {
          return this.device_.selectConfiguration(1);
        }
      })
      .then(() => this.device_.claimInterface(1)) // todo: find correct interface to use
      .then(() => this.device_.controlTransferIn({
          requestType : 'standard',
          recipient : 'device',
          request : 0x01,
          value : 0x00,
          index : 0x00
      }, BYTE_LENGTH))
      .then(() => {
        readLoop();
      });
  };

  disconnect() {
    return this.device_.controlTransferIn({
            requestType : 'standard',
            recipient : 'device',
            request : 0x01,
            value : 0x00,
            index : 0x00
          }, BYTE_LENGTH)
          .then(() => this.device_.releaseInterface(1))
          .then(() => this.device_.close());
  };

  send(data) {
    return this.device_.transferOut(2, data);
  };

  onReceived(parts) {
    var barcode = parts[1];
    this.rawScan_ = ''; // clear out old scan

    // determine type of bar code...
    if (RE_BEACON_NUMBER.test(barcode)) {
      var major_minor = barcode.split('-');
      let result = {
        number: barcode,
        major: parseInt(major_minor[0], 16),
        minor: parseInt(major_minor[1], 16),
      }
      this.emmiter_.emit('beacons', result);
    }
    else {
      try {
        let json = JSON.parse(barcode);
        this.emmiter_.emit('jobOrder', json);
      }
      catch (error) {
        this.emmiter_.emitError('errors', error);
      }
    }
    return;
  }

  onReceivedError(error) {
    return error || null;
  };

  subscriber(name, handler) {
    return this.emmiter_.listen(name, handler);
  }

}

export default serialPort;

export function getPorts() {
  return navigator.usb.getDevices().then(devices => {
    return devices.map(device => new serialPort(device));
  }).catch(err => console.log('oops: ', err));
};

export function requestPort() {
    // { 'vendorId': 0x05e0, 'productId': 0x1701 },
  const filters = [
    { 'vendorId': 0x05e0 },
  ];
  return navigator.usb.requestDevice({ 'filters': filters }).then(
    device => new serialPort(device)
  ).catch(err => console.log('request Device error: ', err));
}



