import Rx from 'rxjs/Rx';

export default class Emmiter {
  constructor() {    
    this.subjects_ = {};
    this.hasOwnProp = {}.hasOwnProperty;
  }

  emit = (name, data) => {
    let streamName = this.createName_(name);
    this.subjects_[streamName] || (this.subjects_[streamName] = new Rx.Subject());
    this.subjects_[streamName].next(data);
  };

  emitError = (name, error) => {
    let streamName = this.createName_(name);
    this.subjects_[streamName] || (this.subjects_[streamName] = new Rx.Subject());
    this.subjects_[streamName].error(error);
  };

  listen = (name, handler) => {
    let streamName = this.createName_(name);
    this.subjects_[streamName] || (this.subjects_[streamName] = new Rx.Subject());
    return this.subjects_[streamName].subscribe(handler);
  };

  dispose = () => {
    let subjects = this.subjects_;
    for (let prop in subjects) {
      if (this.hasOwnProp.call(subjects, prop)) {
        subjects[prop].dispose();
      }
    }

    this.subjects = {};
  };

  createName_ = (name) => {
    return `$_${name}`;
  }
};





